import { AuthService } from './../shared/services/login.service';
import { Component, HostListener, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { routerTransition } from '../router.animations';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { CheckPasswords, CompareWithOldPassword, CheckIfPasswordContainsMinimum } from '../shared/validators/customValidators';
import { SecurityService } from '../shared/services/security.service';
import { Security } from '../shared/models/security';
import { Subscription, map } from 'rxjs';
import { message } from '../shared/utils/user-messages';
import jwt_decode from 'jwt-decode';
import { UserRoleService } from '../shared/services/user-role.service';
import { ProfileService } from '../shared/services/profile.service';
import { GlobalAccountPermissions } from '../shared/DTO/globalAccountPermissions';
import { NotificationsService } from '../shared/services/notifications.service';
import { EsolveSnackbarService } from '../shared/services/esolve-snackbar.service';
import { NgZone } from '@angular/core';
import fnv1a from 'fnv1a';
import { SessionTimerService } from '../shared/services/session-timer.service';
import { CookieService } from 'ngx-cookie-service';
import { AdvancedSSOService } from '../shared/services/advanced-sso.service';

declare let pendo: any;

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    animations: [routerTransition()]
})
export class LoginComponent implements OnInit {

    loginForm: FormGroup;
    resetForm: FormGroup;
    defaultPassword = false;
    passwordValueOnLogin: string;
    forgotPasswordClicked = false;
    emailMessageSubscription: Subscription;
    showEmailMessage = false;
    resetEmailMessage = `If a matching email address is found a reset email will be sent.
    If you do not receive the email please check your spam folder`;
    message; /** Contains standardized user messages */
    passwordExpiryMessage = '';
    passwordExpiring: boolean;
    passwordExpired: boolean;
    passwordExpiredMessage = "Your password has expired, please choose a new password.";

    /** Password format preferences */
    minimumPasswordLength: number;
    passwordValidatorPattern = '';
    passwordValidationMessage = "Password must consist of at least ";
    paternValidationMessageList: string[] = new Array<string>();
    permissions: GlobalAccountPermissions;

    loggedId: number;
    isUser: boolean;
    preferencesChanged = false; //Are security changes made in system preferences

    hashedId = ''; //Property used for hash email with fnv1a function
    encodedEmail = '';
    appVersion = '';
    role = '';
    IsLicensed: string;
    isSuperAdmin: boolean;

    customerName = null;
    minimumUpperCharacters = 0;
    minimumSpecialCharacters = 0;
    minimumNumericCharacters = 0;
    isLoggedInUsingASSO = true;
    timeoutMinutes = 0;
    loginUrl: string;
    isSuccessfullyLoggedIn = true;
    errorMessageForLogin = 'Sorry we couldnâ€™t find you in Auto Rostering. Please contact your system administrator.';

    constructor(
        public router: Router,
        private _fb: FormBuilder,
        private _authService: AuthService,
        private _messageService: EsolveSnackbarService,
        private _securityService: SecurityService,
        private _userRoleService: UserRoleService,
        private _profileService: ProfileService,
        private _notificationsService: NotificationsService,
        private _router: Router,
        private zone: NgZone,
        public sessionService: SessionTimerService,
        private cookieService: CookieService,
        private _http: HttpClient,
        private _advancedSSOService: AdvancedSSOService,
    ) { }    

    ngOnInit() {
        if (!localStorage.getItem('isLoggedInUsingASSO')){
            let organizationHint = "";
            let authDomain = localStorage.getItem('authDomain');
    
            this._advancedSSOService.getSsoLoginDetails()
                .pipe(map((data: any) => data))
                .toPromise()
                .then((res) => {
                    if (res) {
                        organizationHint = res.advancedSSO.organizationHint;
                        authDomain = res.advancedSSO.authenticationDomain;
                        localStorage.setItem('isLoggedInUsingASSO', 'true');
                        localStorage.setItem("organizationHint", organizationHint);
                        localStorage.setItem("authDomain", authDomain);                        
                        localStorage.setItem("authUrl", res.assoUrls.authUrl);
                        localStorage.setItem("authBaseUrl", res.assoUrls.authBaseUrl);
                        organizationHint = res.isOverRuleOrganizationHint ? '' : organizationHint;                        
                        this.redirectToAdvancedPlatformLoginPage(organizationHint, authDomain)
                    }
                });  
        }
        else {
            this.loginUrl = this._authService.getLoginUrl();
            localStorage.removeItem('isLoggedIn');
            localStorage.removeItem('loggedUserName');
            localStorage.removeItem('userCredentials');
            localStorage.removeItem('userImage');
            localStorage.removeItem('jwt');
            localStorage.removeItem('username');
            localStorage.removeItem('id');
            localStorage.removeItem('role');
            localStorage.removeItem('profileId');
            localStorage.removeItem('connectionId');
            localStorage.removeItem('hasLinkedAccount');            
            this.message = message;            
            this.buildForms();
            this._userRoleService.setGlobalAccountPermissions(void 0);
            localStorage.removeItem('isSsoUserAdmin')
            localStorage.removeItem('isSuperAdmin');
            this.getSecurity();
            this.loginUsingASSO();
        }
    }

    ngAfterViewInit() {
        var cookie = this.cookieService.get("ACCESS_TOKEN");
        this.emailMessageSubscription = this.resetForm.controls.username.valueChanges.subscribe((value) => {
            if (value && this.resetForm.controls.username.valid) {
                this.showEmailMessage = true;
            } else {
                this.showEmailMessage = false;
            }
        });
    }

    getSecurity(){
        this._securityService.getSecurity(false).subscribe((securityPreferences: Security) => {
            this.timeoutMinutes = securityPreferences.timeoutMinutes;
        })
    }

    loginUsingASSO(){
        this._authService.login(this.loginForm.value)
            .subscribe((loginResponse: any) => {
                this.IsLicensed = loginResponse.isLicensed;
                this.isSuperAdmin = loginResponse.isSuperAdmin;
                this.isSuccessfullyLoggedIn = loginResponse.isSuccessfullyLoggedIn;                
                //Regular Login
                if (loginResponse.isSuccessfullyLoggedIn && !loginResponse.message) {
                    localStorage.setItem('IsLicensed', loginResponse.isLicensed);
                    localStorage.setItem('loggedUserName', loginResponse.nameAndSurname);
                    localStorage.setItem('jwt', loginResponse.accessToken.token);
                    // Set JWT token received from login api into cookie with expiration same as token
                    const authDomain = localStorage.getItem("authDomain");
                    this.cookieService.delete('AR_TOKEN', '/', '.' + authDomain);
                    const dateNow = new Date();
                    dateNow.setMinutes(dateNow.getMinutes() + this.timeoutMinutes);
                    this.cookieService.set('AR_TOKEN', loginResponse.accessToken.token, dateNow,'/','.' + authDomain);
                    // Set user's sso admin right
                    localStorage.setItem('isSsoUserAdmin', loginResponse.isSSOUserAdmin);
                    if (loginResponse.image) {
                        localStorage.setItem('userImage', loginResponse.image);
                    }
                    localStorage.setItem('id', this.getDecodedAccessToken(loginResponse.accessToken.token).nameid);
                    localStorage.setItem('role', this.getDecodedAccessToken(loginResponse.accessToken.token).role);
                    localStorage.setItem('username', loginResponse.accessToken.username);
                    localStorage.setItem('hasLinkedAccount', loginResponse.accessToken.hasLinkedAccount);                    
                    if (localStorage.getItem('id') && Number(localStorage.getItem('id')) > 0) {
                        setTimeout(() => {
                            this._authService.onSuccesfulLogin.next(true);
                        }, 100);
                    }
                    //Resetting Observable's value so that closed modals can be opened again.
                    this._userRoleService.closeModalUponLogout(false);
                    this.customerName = loginResponse.customerName;
                    localStorage.setItem('customerName', this.customerName);
                    this.encodedEmail = loginResponse.id;
                    this.appVersion = loginResponse.rosteringAppVersion;
                    this.onLoggedin(loginResponse.accessToken.token, loginResponse.nameAndSurname);
                    this._notificationsService.shouldUpdateUnreadNotifications.next(true);
                    localStorage.setItem('isSuperAdmin', loginResponse.isSuperAdmin);
                }
                else if(!this.isSuccessfullyLoggedIn){
                    this.isSuccessfullyLoggedIn = false;
                    this._messageService.open(this.errorMessageForLogin);
                }
            },
                (err: HttpErrorResponse) => {
                    localStorage.removeItem('isLoggedInUsingASSO');
                    if (err.error == "Customer license is expired" || err.error == "Access Denied") {
                        this._router.navigateByUrl("/access-denied", {
                            state: {
                                source: 'license'
                            }
                        });
                    } 
                    else if(err.error[0].key == "The Request is invalid"){
                        this.isSuccessfullyLoggedIn = false;
                        this.errorMessageForLogin = err.error[0].value;
                        this._messageService.open(this.errorMessageForLogin);
                    }
                    else{
                        this.isSuccessfullyLoggedIn = false;
                        this._messageService.open(this.errorMessageForLogin);
                    }
                });
    }
    
    redirectToAdvancedPlatformLoginPage(organizationHint: string, authDomain: string) {        
        const baseUrl = window.location.origin;
        const pathname = window.location.pathname;
        let authDomainUrl = "";

        var authBaseUrl = localStorage.getItem("authBaseUrl");

        this._http.get('./assets/config.json')
            .pipe(map((res: any) => res))
            .toPromise()
            .then(config => {
                if (authDomain !== undefined && authDomain !== "oneadvanced.io")
                    authDomainUrl = '&authenticationDomain=' + authDomain;

                window.location.href = authBaseUrl + config.AUTH_URL +
                    '?redirectUri=' + baseUrl + pathname
                    + (organizationHint.length > 0 ? '&organizationHint=' + organizationHint : '')
                    + authDomainUrl;
            });
    }

    buildForms() {
        this.buildLoginForm();
        this.buildResetForm();
    }

    buildLoginForm() {
        this.loginForm = this._fb.group({
            username: [null, { validators: [Validators.required] }],
            oldPassword: [null, { validators: [Validators.required] }],
            newPassword: [null],
            confirmNewPassword: [null],
            isSuccessfullyLoggedIn: [null],
            message: [null],
            isFirstLogin: [null],
            isForgottenPassword: [false],
            sendSystemAccessMail: [false]
        },
            {
                validators: [
                    CheckPasswords("newPassword", "confirmNewPassword"),
                    CompareWithOldPassword("oldPassword", "newPassword")
                ]
            });
    }

    buildResetForm() {
        this.resetForm = this._fb.group({
            username: [null],
            isForgottenPassword: [true],
            sendSystemAccessMail: [false]
        });
    }
    getDecodedAccessToken(token: string): any {
        try {
            return jwt_decode(token);
        } catch (Error) {
            return null;
        }
    }            

    onLoggedin(encodedToken: any, nameAndSurname: any) {
        if (!this.defaultPassword) {
            localStorage.setItem('isLoggedIn', 'true');
            localStorage.setItem('userCredentials', this.loginForm.controls.username.value);

            this._userRoleService.changeRole(this.getDecodedAccessToken(encodedToken).role);
            this._userRoleService.changeProfile(this.getDecodedAccessToken(encodedToken).ProfileID);
            this._userRoleService.changeId(this.getDecodedAccessToken(encodedToken).nameid);
            this._userRoleService.changeAccountCredentialsAndImage(nameAndSurname);

            this.initializePendo();
            this._authService.isSessionExpireDialogOpen.next(false);
            this.sessionService.startTimer(this.timeoutMinutes);
            this.router.navigate(['/dashboard']);
        }
    }

    get f() {
        if (this.loginForm) {
            return this.loginForm.controls;
        }
    }

    get rf() {
        if (this.resetForm) {
            return this.resetForm.controls;
        }
    }    
    
    initializePendo(): void {
        let decodedEmail = btoa(this.encodedEmail);
        this.hashedId = fnv1a(decodedEmail).toString();
        if (
            (this.hashedId && this.hashedId !== '') &&
            (this.appVersion && this.appVersion !== '')
            // && (this.customerName && this.customerName !== '')
        ) {
            const metadataObject = {
                visitor: {
                    id: this.hashedId,
                    ro_role: localStorage.getItem('role'),
                    ro_app_version: this.appVersion
                },
                account: {
                    id: this.customerName
                }
            }
            if (!this._authService.getPendoInitialized()) {
                pendo.initialize(metadataObject);
                this._authService.setPendoInitialized();
            } else {
                pendo.updateOptions(metadataObject);
            }
        }
    }

    public getCookie(name: string) {
        let allCookies: Array<string> = document.cookie.split(';');
        let allCookiesLength: number = allCookies.length;
        let cookieName = `${name}=`;
        let cookie: string;

        for (let i: number = 0; i < allCookiesLength; i += 1) {
            cookie = allCookies[i].replace(/^\s+/g, '');
            if (cookie.indexOf(cookieName) == 0) {
                return cookie.substring(cookieName.length, cookie.length);
            }
        }
        return '';
      }
      
    public returnToLoginPage(){
        this._authService.returnToLoginPage();
    }
}
